@import url('https://fonts.googleapis.com/css2?family=Grape+Nuts&display=swap');

.logo {
  font-family: 'Grape Nuts', cursive;
  font-weight: 800;
  font-size: 24px;
}

.mobile-unsupport {
  display: none;
}

@media only screen and (max-width: 760px) {
  .main-layout {
    display: none;
  }
  .mobile-unsupport {
    display: block;
  }
}

/*
 * Links
 */
.link {
  position: relative;
  display: inline-block;
  font-size: 18px;
  line-height: 1;
  font-family: 'Source Sans Pro', sans-serif;
  text-decoration: none;
}

.link--1.window::before {
  content: '';
  display: block;
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  pointer-events: none;
  background: #747474;
  transform: scale3d(0, 1, 1);
  transform-origin: 100% 50%;
  transition: transform 500ms cubic-bezier(0.86, 0, 0.07, 1);
  will-change: transform;
}

.link--1:focus::before,
.link--1:hover::before {
  transform: scale3d(1, 1, 1);
  transform-origin: 0% 50%;
}

@-webkit-keyframes reveal {
  0%,
  90% {
    bottom: -30px;
  }

  100% {
    bottom: 15px;
  }
}

@keyframes reveal {
  0%,
  90% {
    bottom: -30px;
  }

  100% {
    bottom: 15px;
  }
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #88888844;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #88888844;
}

